<style lang="scss" scoped>@import 'core';
    $height-mobile: 60px;
    $height-tablet: 80px;
    $height-desktop: 100px;

    div {
        height: 300px;
        position: relative;
        margin-bottom: $height-mobile;
    }

    header {
        border: 15px solid green;
        padding: 40px 0;
        position: absolute;
        top: 300px;
        width: 100%;
        @include bp-prop('height', $height-mobile, false, $height-tablet, $height-desktop);

        &.is-docked {
            margin-top: 0;
            position: fixed;
            top: 0;
        }
    }
</style>

<template>
    <div>
        <header :class="{
            'is-home': Router.current.path === '/',
            'is-docked': Window.scrollTop > 300,
        }">
            hello
        </header>
    </div>
</template>

<script>
    import RouterState from 'src/state/router';
    import WindowState from 'src/state/window';

    export default {
        data() {
            return {
                Router: RouterState.state,
                Window: WindowState.state,
            };
        },
    };
</script>



